.pdf {
	// background: pink
	padding: 40px;
	&__footer {
		margin-top: 235px;
		.logo {
			img {
				width: 280px; } }
		.flex-r {
			width: 50%; } }
	&__contacts {
		span {
			display: block;
			font-size: 29px;
			letter-spacing: .73px;
			@include r-regular; } }
	&__priceInfo {
		margin-top: 60px;
		h1 {
			font-size: 48px;
			@include r-medium; }
		h2 {
			font-size: 36px;
			@include r-regular;
			letter-spacing: .39px; }
		> div {
			display: inline-block;
			padding-right: 40px;
			margin-right: 40px;
			position: relative;
			> span {
				font-size: 21px;
				@include r-regular; }
			&:not(:last-child) {
				&:after {
					content: '';
					width: 1px;
					height: 100%;
					background: lighten(#aeaeae, 3%);
					position: absolute;
					right: 0;
					top: 0; } } }
		sup {
			left: -4px;
			font-size: 65%; } }
	&.card {
		&__additionals,.ul-for-text li span,p {
			font-size: 21px;
			letter-spacing: .38px;
			line-height: 30px; }
		.ul-for-text li {
			margin: 25px 0; } }
	h3 {
		font-size: 25px; }
	aside {
		top: 0; }
	// .container
	/// min-width: 100%
	.rieltor-panel,.card__aside {
		margin: 0; }
	.rieltor-panel {
		border: 1px solid #9f9f9f;
		&:after {
			display: none; }
		&.asideLink {
			li {
				&:not(:last-child) {
					font-size: 19px; }
				.b-link {
					font-size: 17px;
					color: #007af6; } }
			.rieltor-panel__Fio {
				font-size: 19px;
				width: 100%; } } }
	.rieltor-panel,.cardAside {
		box-shadow: none; }
	.cardAside {
		padding: 0;
		background: #fff;
		h5 {
			font-size: 25px;
			padding-top: 25px;
			letter-spacing: .63px;
			margin-bottom: 12px; }
		&__features {
			margin-bottom: 0;
			ul li {
				background-size: 6px 3px; }
			span {
				padding-right: 5px;
				font-size: 21px; } } } }

.pdf-images {
	&__main {
		height: 450px;
		position: relative;
		img {
			@include coverdiv; } }
	&__additionals {
		img {
			display: inline-block;
			width: calc((100% / 3) - 16px);
			height: 145px;
			margin-top: 23px;
			&:not(:nth-child(3n)) {
				margin-right: 20px; } } } }
