$color: $green;
$m-color: #f7921d;
$line: #aeb6bf;
$dot: #aeb6bf;
$dot-xl: 60px;
$dot-sm: 50px;
$dot-size: 12px;

.tl {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	width: 100%;
	position: relative;
	&_btns,
	&_c-w {
		width: 100%; }
	&_c-w {
		margin-top: 70px; }
	&_c {
		height: 0;
		overflow: hidden;
		visibility: hidden;
		opacity: 0;
		transition: opacity .65s cubic-bezier(1, .9, 0.1, 0.1);
		&.active {
		  height: auto;
		  overflow: visible;
		  visibility: visible;
		  opacity: 1; } }
	&_btns {
		margin-bottom: 50px;
		padding: 0;
		position: relative;
		padding-right: 15px;
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		&.last {
		  &:after {
		    color: $color; } }
		&:after {
			content: "\e917";
			font-family: "custom-iconfont";
			position: absolute;
			right: 0;
			top: 50%;
			font-size: 11px;
			color: #aeb6bf;
			transform: translateY(-50%) rotate(-90deg); }
		li {
			list-style-type: none;
			flex: 1 auto;
			position: relative;
			text-align: center;
			color: white;
			padding: 10px 0;
			user-select: none;
			cursor: pointer;
			&:before,
			&:after {
				content: '';
				transition: background .47s linear; }
			span {
				color: $text;
				position: absolute;
				left: 50%;
				padding-top: 30px;
				transform: translateX(-50%);
				font-size: 20px;
				line-height: 1;
				transition: all .25s cubic-bezier(.1, .5, 0.55, 0.7);
				letter-spacing: .63px;
				@include r-regular; }
			&:last-child {
				&:after {
				  width: 165px; } }
			&:before {
				width: $dot-size;
				height: $dot-size;
				display: block;
				margin: 0 auto;
				border-radius: 50%;
				background-color: $dot; }
			&:after {
				position: absolute;
				width: 100%;
				height: 2px;
				background: $line;
				top: 50%;
				left: -50%;
				transform: translateY(-50%);
				z-index: -1; }
			&:first-child:after {
				content: none; }
			&.original-active {
				span {
					color: $green;
					font-size: 24px;
					padding-top: 40px;
					@include r-medium; } }
			&.active {
				&:before {
				  background: $color; }
				+ li.active:after {
				  background: $color; } } } } }

@media (max-width: 1200px) {
  .tl {
    &_btns {
      margin-bottom: 0;
      &:after {
        display: none; }
      li {
        flex: 1 0 15%;
        padding: 0;
        &:after,
        &:before {
        	transition: background .2s ease-out; }
        &.active {
        	&:before {
        		background: $m-color; } }
        span {
        	padding-top: 0 !important;
        	top: 20px;
        	font-size: 19px !important;
        	color: #fff !important; }
        &:nth-child(5n+1) {
          margin-bottom: 15px; }
        &:before {
          margin-bottom: 20px;
          width: $dot-xl;
          height: $dot-xl; }
        &:after {
          display: none; } } }
    &_c-w {
    	margin-top: 40px; } } }

@media (max-width: 480px) {
	.tl {
		&_c-w {
			margin-top: 20px; }
		&_btns {
			li {
				flex: 1 0 25%;
				span {
					font-size: 16px !important;
					top: 17px; }
				&:before {
					margin-bottom: 3px;
					width: $dot-sm;
					height: $dot-sm; } } } } }
