.u-form {
	input, textarea {
		&:not([type="submit"]) {
			&:focus {
				border: 1px solid $orange; } } } }
.popup,.u-form {
	input, textarea {
		outline: none;
		&:not([type="submit"]) {
			color: #fff; }
		&:not([type="submit"]) {
			@include r-regular;
			font-size: 16px;
			text-align: left;
			color: lighten($text,25%); }
		&:focus:required:invalid {
			border: 1px solid $orange; }
		&:required:valid {
			border-color: darken($green,1%);
			color: $green; } }
	form {
		padding: 0 80px;
		input,textarea {
			margin-top: 10px; }
		input,.btn {
			height: 40px;
			line-height: 40px; } } }

.popup {
	position: fixed;
	top: -5vh;
	left: -5vw;
	width: 110vw;
	height: 110vh;
	background: rgba(0,0,0,.7);
	z-index: 99999;
	visibility: hidden;
	form {
		ul + .pp {
			margin-top: -7px; }
		input.realCheckbox+label:before {
			width: 33px;
			height: 25px;
			border-color: #c7c7c7; } }
	.close-trigger {
		color: $orange; }
	&:target {
		visibility: visible;
		.popup__outer {
			transform: translate(-50%,-55%);
			opacity: 1; } }
	&__outer {
		position: absolute;
		top: 50%;
		left: 50%;
		@include nosel;
		transition: all .4s ease-out;
		transform: translate(-50%,-45%);
		opacity: 0; }
	&__inner {
		width: 480px;
		padding: 10px;
		padding: 40px 0;
		background: #fff; }
	&__review {
		.popup__title {
			padding: 0 0 1px 0; } }
	&__title {
		padding: 0 45px 15px;
		h4 {
			margin-bottom: 7px;
			text-align: center; }
		p {
			line-height: 22px;
			margin: 0;
			text-align: center; } }
	input[name="popup__permission"] {
		margin-top: 0; }
	&__permission {
		font-size: 14px;
		letter-spacing: 0.14px;
		line-height: 19px;
		margin: 12px 0;
		transition: $tr-fast;
		@include r-light;
		&:hover,&:active {
			a {
				color: $orange;
				border-color: $orange; } }
		p {
			margin: 0; }
		a {
			border-bottom: 1px solid lighten( #676767, 20%);
			color: #202020;
			@include r-light; } }
	.choose-block {
		text-align: left; } }

form {
	position: relative;
	ul li {
		display: block;
		position: relative; }
	input,textarea {
		width: 100%;
		border: 1px solid #aaa;
		outline: none;
		display: block;
		border-color: #c6c6c6;
		color: darken(#818181, 15%);
		transition: $tr-fast;
		text-align: left;
		padding-left: 17px;
		&::placeholder {
		    color: #818181;
		    letter-spacing: .8px;
		    font-size: 15px;
		    @include r-regular; } }
	.btn__submit {
		width: 100%;
		padding: 0;
		border: none !important; } }

textarea,
input[type="text"] {
	text-align: left !important;
	&::placeholder {
		text-align: left !important; } }
input {
	&:not([type="submit"]) {
		@include r-regular;
		font-size: 16px;
		text-align: left;
		color: lighten($text,25%) !important; } }
// TABLES
table {
  border: 1px solid #ccc;
  border-collapse: collapse;
  margin: 0;
  padding: 0;
  width: 100%;
  table-layout: fixed;
  &.light-theme {
  	tr {
  		background: #fff;
  		border: 1px solid $lbd;
  		th,td {
  			border-right: 1px solid $lbd; } } }
  &.dark-theme {
  	tr {
  		background: #fafafa; } }
  caption {
    font-size: 26px;
    margin: .5em 0 .75em;
    caption-side: top;
    color: $text;
    @include r-regular; }
  tr {
    border: 1px solid #ddd;
    padding: .35em; }
  th, td {
    padding: .625em;
    text-align: center; }
  th {
    font-size: .85em;
    letter-spacing: .1em;
    text-transform: uppercase; } }

@media screen and (max-width: 600px) {
  table.resp {
    border: 0;
    caption {
      font-size: 24px; }
    thead {
      border: none;
      clip: rect(0 0 0 0);
      height: 1px;
      margin: -1px;
      overflow: hidden;
      padding: 0;
      position: absolute;
      width: 1px; }
    tr {
      border-bottom: 3px solid #ddd;
      border-right: none !important;
      display: block;
      margin-bottom: 10px;
      padding: 0; }
    td {
      border-bottom: 1px solid #ddd;
      display: block;
      font-size: 14px;
      text-align: right;
      &::before {
        content: attr(aria-label);
        content: attr(data-label);
        float: left;
        font-weight: bold;
        text-transform: uppercase; }
      &:last-child {
        border-bottom: 0; } } } }

.card__itType {
	.itItem {
		&:hover {
			z-index: 10;
			.itItem__tooltip {
				visibility: visible;
				opacity: 1;
				transform: translate(-53%,0); } } }
	.itItem {
		z-index: 5;
		cursor: pointer;
		&__tooltip {
			visibility:  hidden;
			opacity: 0;
			transform: translate(-53%,15px);
			transition: all .25s ease-out;
			position: absolute;
			bottom: -55%;
			left: 100%;
			margin-bottom: 5px;
			margin-left: -80px;
			padding: 15px 12px 20px;
			width:  49vh;
			background-color: #fff;
			box-shadow: 0 2px 6px 1px rgba(0,1,1,.2);
			color: $text;
			text-align: center;
			z-index: 1;
			cursor: default;
			.itItem__subtitle {
				padding: 0 0 10px; }
			p {
				margin-bottom: 0; } } } }

.attach-file {
	padding-top: 5px;
	display: block;
	&:hover {
		span, i {
			color: $text; } }
	span {
		display: inline;
		color: lighten($text,20%);
		cursor: pointer; }
	&__text {
		color: $text;
		font-size: 15px;
		padding-left: 10px; }
	&__icon {
		width: 30px;
		height: 30px;
		position: relative; }
	i {
		font-size: 13px;
		color: lighten($text,20%);
		&:before {} }

	input {
		@include vh; } }
