				top: -10px; } } }
	aside.rightSide-aside {
		margin: 15px 0 0;
		width: 100% !important;
		.submit {
			padding: 20px 25px; } }
	.submit {
		line-height: 17px;
		&__title {
			font-size: 15px; } }
	.m0 {
		line-height: 1.22; }
	.ul-for-text {
		li {
			margin-bottom: 20px; } }
	.card__body {
		.mobile {
			display: block; }
		.ul-for-text li {
			&:first-child {
				margin-bottom: 0; } } }
	.inner-container h2 {
		line-height: 1.22; }
	.text-block {
		p {
			padding-bottom: 10px; } }
	.b-crumbs {
		padding: 20px 0 !important; }
	.nav li {
		margin: 0 10px 10px 0; }
	.inner-container {
		width: 100%; }
	.feedback {
		.btn__green {
			i {
				display: block; }
			span {
				display: none; } } }
	.options {
		width: 100.3%; }
	.f-db {
		margin-right: 0 !important; }
	.filter--buildingType {
		width: 100%; }
	.filter--duration {
		margin: 30px 0 !important; }
	.header,
	.footer {
		overflow: hidden; }
	.itType {
		&.home {
			.itItem {
				padding: 30px 10px 20px;
				&__title-img {
					width: 65%; } }
			.col-6 {
		 		margin-bottom: 20px; } } }
	.itItem {
		&__title-img {
			width: 65%; }
		&__subtitle {
			font-size: 16px;
			line-height: 20px; } }
	.order-panel {
		padding-bottom: 0;
		&__title {
			display: none; }
		.layout-switchers {
			display: none; }
		.orderers {
			.btn {
				margin-left: 0;
				margin-right: 30px; } }
		.btn {
			margin-bottom: 3vh; } }
	.choosed-tags .remove-trigger, .choosed-tags span {
		font-size: 15px; }
	#on-top {
		bottom: 5vh;
		right: 5vh; }
	.item {
		&__controls {
			display: none; } }
	.dropdown {
		&.is-active {
			z-index: 100;
			.dropdown__content {
				z-index: 100;
				position: relative; } } }
	.btn {
		&__green,&__orange,&__gray {
			&:hover {
				box-shadow: none; }
			&:active {
				box-shadow: 0 2px 4px 1px rgba(0, 1, 1, .3); } } }
	.section {
		h2 {
			margin-top: 50px; } }
	.mobile {
		&.btn-group {
			display: flex;
			.btn {
				text-align: center;
				width: 47%;
				&:first-child {
					margin-right: 6%; } } } }
	#show-menu,
	#show-submenu {
		display: inline-block; }
	.collapse {
		display: none; }
	.header-bottom-nav {
		margin-top: 20px;
		background: #fff;
		flex-direction: column;
		align-items: flex-start;
		border: 1px solid #ebebeb;
		a {
			display: block;
			width: 100%;
			padding: 2px 20px;
			text-align: left;
			color: $text;
			&:after,&:before {
				display: none; }
			&:not(:last-child) {
				border-bottom: 1px solid #ebebeb; }
			&.active, &:hover {
				background: #ddd;
				&:after {
					display: none; } } } }
	.header {
		&__inner {
			&.is-opened {
				height: 130vh;
				.header__top {
					height: 100% !important; }
				.header-top-nav {
					display: block;
					box-shadow: inset 0 2px 5px rgba(0,0,1,.15);
					visibility: visible;
					height: 100%;
					ul {
						transform: translate(0); } } } }
		&__top {
			position: fixed;
			width: 100%;
			height:  78px;
			z-index: 202;
			overflow: hidden;
			transition: all .6s ease;
			box-shadow: 0 2px 5px rgba(0, 0, 1, 0.15);
			ul {
				flex-direction: column; } }
		&__bottom {
			padding: 100px 0 20px;
			background: #fff;
			position: relative;
			box-shadow: none;
			.collapse {
				display: none;
				&.show {
					display: block; } }
			>.container {
				position: relative; }
			.feedback {
				transform: none;
				top: 0;
				position: absolute;
				flex-direction: row-reverse;
				display: inline-flex;
				.i-control-cart {
					transform: none;
					margin: 0 15px 0 0; } } } }
	.header-top-nav {
		position: absolute;
		width: 100%;
		height: 100%;
		left: 0;
		top: 0;
		margin-top: 77px;
		padding: 0;
		background: #fff;
		z-index: -1;
		height: 0;
		visibility: hidden;
		overflow: hidden;
		transition: all .6s ease;
		ul {
			transform: translateY(-110%);
			transition: all .35s ease-out;
			li {
				margin: 0 !important;
				display: block;
				font-size: 25px;
				padding: 0 27px;
				border-bottom: 1px solid #ebebeb;
				a {
					line-height: 2.5; } } } }
	.footer {
		.f-list-group ul {
			margin: 0; }
		&__outer {
			width: 100%; }
		&__add-column {
			.links,.footer__socials {
				display: inline-block; } }
		.footer__socials {
			line-height: 0;
			vertical-align: bottom;
			float: right;
			> a {
				margin-top: 0; } }

		&__copyright-before2 {
			margin: 5vh 0 4vh; }
		.footer-contacts {
			margin-bottom: 3vh; } }
	.filters-form {
		max-width: 100vw;
		overflow: hidden;
		position: relative;
		background: #fff;
		.flex-r {
			justify-content: flex-start !important; }
		&__total {
			padding-left: 0;
			margin: 60px 0 0;
			span {
				font-size: 28px; }
			.btn-group {
				margin-top: 25px;
				.btn {
					flex: 1 1 auto;
					padding: 0;
					height: 70px;
					line-height: 70px;
					font-size: 24px; } } }
		.level-filter {
			>.flex-r {
				flex-direction: column !important;
				align-items: flex-start !important;
				.f-c {
					margin-bottom: 20px; } } }
		input[type=number] {
			width: 100% !important; }
		.additional-options {
			.dropdown__content {
				width: 100%;
				margin: 0;
				border-top: none; }
			.btn {
				width: 100% !important;
				color: #303030;
				&__white {
					&:after {
						opacity: .5; } } } } }
	.filters {
		margin-bottom: 50px; }
	.filter {
		margin-bottom: 33px;
		margin-right: 0;
		&__rooms {
			width: auto !important;
			label:not(.filter__title) {
				margin-left: 15px; } }
		&__onlyNew {
			position: absolute;
			padding-top: 125px;
			z-index: 10; } }
	.big-options {
		padding-bottom: 125px;
		&.is-active {
			padding-bottom: 30px; } } }
@media screen and (max-width: 1200px) and (min-width: 570px) {
	.success-stories {
		.rieltor-panel.link {
			&__topSide {
				display: flex;
				flex-flow: wrap row;
				align-items: center; }
			&:after {
				display: none; }
			&__bottomSide {
				text-align: left;
				margin-top: 10px; } } }

	.filter--buildingType, .filter--duration, .filter--payback {
		.select {
			height: 75px;
			line-height: 75px; } }
	.filter__title,
	.filters-set__title {
		font-size: 24px; }
	.filters-form {
		input.realCheckbox+label:after, input.realCheckbox+label:before, input[type=radio]+label:after, input[type=radio]+label:before {
			width: 50px;
			height: 50px; }
		input.realCheckbox+label:after, input[type=radio]+label:after {
			line-height: 50px;
			font-size: 22px; }
		.dropdown__trg, .fstResultItem, .options li,
		.select, input,  select,.SelectBox .placeholder,
		input.realCheckbox+label, input[type=radio]+label {
			font-size: 24px; }
		.cSelect,	.options li, input,  select {
			height: 75px;
			line-height: 75px; }
		.big-options {
			&__list {
				ul {
					max-height: 44vh; } }
			.search,.search input, {
				height: 50px;
				line-height: 50px; }
			.search {
				margin: 10px 0;
				input {
					&::placeholder {
						font-size: 20px; } } }
			.btn__dis {
				height: 35px;
				line-height: 35px;
				width: 100%;
				font-size: 20px; } }
		&__bottom {
			.btn {
				height: 75px;
				line-height: 75px; } }
		&__top {
			.SumoSelect>.CaptionCont>span,
			.text-checkbox+label {
				font-size: 24px; }
			.SumoSelect {
				width: 120px;
				&.open {
					>.optWrapper {
						width: 92.7vh;
						top: 85px;
						&.multiple>.options li.opt {
							height: 65px;
							line-height: 65px; } } }
				>.CaptionCont {
					height: 76px;
					>label:after {
						font-size: 6px; }
					>span {
						line-height: 60px; } } } }
		input.text-checkbox+label {
			padding: 25px 30px; }
		input[type=number] {
			padding: 37px 30px; } } }

@include media-breakpoint-down(md) {
	.full-section-card {
		overflow: hidden; }
	.payment-block__filterRow {
		margin-top: -10px; }
	section.itType {
		margin-top: 0; }
	.card__topSlider {
		margin-bottom: 30px; }
	.item .slider img {
		height: 25vh !important; }
	.c-Slider .s-a {
		display: none !important; }
	.b-crumbs + h2 {
		margin-top: -5px; }
	.contacts-block {
		&__location {
			font-size: 19px; }
		&__title {
			font-size: 15px;
			margin-bottom: 3px; }
		&__top {
			margin-bottom: 10px; }
