.filters {
  &.mapOpen {
    .order-panel {
      display: none; } } }

.filters-form {
  background: #fafafa;
  padding: 30px 0 15px;
  &.additional-rangers {
    input[type=number] {
      font-size: 18px;
      height: 50px;
      @include r-regular;
      color: #303030 !important;
      box-shadow: none; } }
  .hidden-filters {
    .filter {
      display: none; } } }

.filter__layout {
  .flex-r {
    input[type=number] {
      // width: 100%
      border-right: 1px solid $lbd; } } }
.filters-form,
.request-form {
  &__mobile-head {
    margin: 40px 0 60px;
    position: relative;
    h3 {
      font-size: 32px;
      text-align: center; }
    .close-trigger {
      top: -7px;
      font-size: 28px; } }
  input,.btn, .options li,.cSelect,select {
    height: 40px;
    line-height: 40px; }
  .btn {
    padding: 0 13px 0 15px; }
  input,.select,.options li,
  .dropdown__trg,select,.fstResultItem {
    border: 1px solid $lbd;
    font-size: 14px;
    background: #fff; }
  input:not(.location-search__s-input) {
    display: inline-block;
    text-align: center;
    padding: 0 5px 0 15px;
    color: #303030 !important; }
  input[type="submit"] {
    color: #fff !important; }
  input[type="number"] {
    padding: 0;
    width: 90px;
    padding-left: 16px; }
  &__top {
    display: flex;
    align-items: flex-end;
    .text-checkbox {
      + label {
        font-size: 14px; } }
    .SumoSelect {
      width: 70px;
      position: relative;
      &.is-active {
        .CaptionCont {
          background: $orange;
          border-color: #fafafa;
          span {
            color: #fff; }
          label {
            background: $orange;
            &:after {
