      >.optWrapper {
        top: 38px;
        >.options {
          overflow: visible; } }
      >.CaptionCont {
        height: 39px;
        >label {
          background: #fff; }
        >span {
          padding-right: 7px;
          font-style: normal;
          color: $text;
          @include r-regular;
          font-size: 14px; } }
      >.optWrapper.multiple>.options li.opt {
        border-right: none;
        padding: 0 20px 0 40px;
        &:hover {
          background-color: transparent !important; } }
      &.open>.optWrapper {
        width: auto;
        border-top: none; } } }
  &__bottom {
    margin-top: 15px;
    .option-filter {
      position: relative; }
    .level-filter {
      .flex-r {
          .f-c {
            position: relative;
            width: 35%;
            justify-content: center; } } } }
  &__total {
    justify-content: flex-end;
    align-items: flex-start;
    padding: 0 0  1px 15px;
    height: 100%;
    span {
      width: 100%;
      text-align: left;
      color: #535353; }
    .btn-group {
      margin-top: 20px;
      width: 100%;
      .btn__gray {
        &:hover {
          box-shadow: none;
          background: $green; } }
 }      // justify-content: space-evenly
    .btn {
      width: 170px;
      display: inline-block;
      font-size: 15px;
      text-align: center;
      padding: 0;
      span {
        color: #fff; }
      &__green {
        margin-left: 15px; } } }
  &__tags {
    font-size: 13px;
    margin-top: 17px;
    line-height: 1.5;
    > {
      a,strong {
        display: inline-block; } }
    > strong {
      margin-bottom: 1px; }
    > a {
      position: relative;
      font-size: 13px; }
    > a:not(.d-link) {
      color: $blue;
      border-bottom: 1px solid transparent;
      &.is-active, &:hover {
        border-color: $blue; } }
    .d-link {
      color: $text;
      border-bottom: 1px solid $text;
      @include r-light;
      &:hover {
        color: $orange;
        border-color: $orange; } } }

  .filter__rooms,
  .additional-options > .btn {
    width: 250px; } }

.filter,.filters-set {
  height: max-content;
  position: relative;
  &__title {
    letter-spacing: .35px;
    @include r-regular;
    margin-bottom: 8px;
    font-size: 14px;
    sup {
      left: -3px;
      font-size: 62%;
      top: -5px; } } }

.filter--duration,.filter--payback,
.filter--buildingType {
  .select {
    height: 40px;
    line-height: 40px; } }

.filter--duration {
  margin-left: 17px;
  margin-right: 0 !important;
  .select {
    padding-left: 33px;
    width: 100%; } }
.filter--payback,.filter--buildingType {
  margin: 0;
  width: 100%;
  .select,.s_select {
    width: 100%; } }
.filter {
  margin-right: 15px;
  &__onlyNew {
    margin: 0 0 7px 3px;
    label {
      color: #535353; } } }
.filter--buildingType {
  width: 91%; }
.dropdown {
  &__content {
    position: absolute;
    background: #fff;
    border: 1px solid #c6c6c6;
    z-index: 99;
    opacity: 0;
    visibility: hidden;
    z-index: 1; }
  &__trg {
    z-index: 99;
    width: 100%; }
  &.is-active {
    .dropdown__content {
      opacity: 1;
      visibility: visible;
      z-index: 100; }
    .dropdown__trg {
      border: 1px solid $orange;
      z-index: 101; } } }

.additional-options {
  .select {
    width: 100%; }
  .btn {
    color: #9e9e9e;
    letter-spacing: .35px;
    margin-right: 15px;
    &__white {
      position: relative;
      &:after {
        content: "\e907";
        position: absolute;
        right: 20px;
        top: 50%;
        transform: translateY(-50%);
        font-size: 21px;
        opacity: .4;
        font-family: $if; } } }

  .dropdown__content {
    width: 557px;
    padding: 23px 13px 15px;
    margin-top: -1px;
    .filter {
      margin-right: 0; }
    .filters-set {
      .option-filter {
        width: 50%;
        &:not(:first-child) {
          margin-top: 15px; }
        &:first-child {
          z-index: 1; }
        &:second-child {
          z-index: 2; } }
      &:not(:first-child) {
        margin-top: 20px; } } } }


.big-options {
  .btn {
    &__dis {
      position: absolute;
      bottom: 0;
      left: 0;
      padding-left: 35px;
      height: 20px;
      line-height: 20px;
      background: #fff;
      font-size: 15px; } }
  &__list {
    position: relative;
    height: 100%;
    ul {
      max-height: 185px;
      margin: 10px 0 20px;
      @include sbar; } }
  .dropdown {
    &__content {
      width: 100%;
      border-top: none;
      padding: 12px 11px 9px; } }
  .search input {
    text-align: left;
    border: 1px solid $lbd;
    width: 100%; }
  .remove-trigger {
    margin-bottom: 10px; }
  ul {
    li {
      margin-top: 5px; } } }

.u-form {
  input {
    text-align: left !important;
    &[type="radio"] + span {} }
  &__ul {
    li {
      margin-bottom: 15px; } }
  .options {
    width: 100.4%; }
  .radio-block {
    > span {
      display: block; }
    > div {
      display: inline-block;
      margin: 0 15px 0 0; }
    label {
      display: inline-block;
      vertical-middle: baseline;
      font-size: 13px;
      width: 100%;
      span {
        font-size: 16px;
        letter-spacing: .4px; }
      &:before {
        vertical-align: middle; }
      + input {
        @include r-regular;
        line-height: 2; } } }
  label > span, li > span {
    display: block;
    @include r-medium;
    line-height: 2.2;
 } }    // color: lighten($color, 10%)
//priceSlider customs styles
.rangeSlider {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  background-color: transparent;
  input[type=number] {
    padding-bottom: 3px; } }
.noUi-horizontal .noUi-handle {
  width: 15px;
  height: 15px;
  background: #fff;
  border: 1px solid $lbd;
  cursor: pointer;
  box-shadow: none;
  border-radius: 50%;
  &:after,&:before {
    display: none; } }
.noUi-horizontal {
  height: 3px;
  border: none;
  box-shadow: none; }
.noUi-connects {
  border: none; }
.noUi-connect {
  background: $orange;
  border: none;
  height: 2px; }
html:not([dir=rtl]) .noUi-horizontal .noUi-handle {
  right: -12px;
  &.noUi-handle-upper {
    right: -3px; } }

.s-hidden {
  visibility: hidden;
  padding-right: 10px; }
.select {
  cursor: pointer;
  display: inline-block;
  position: relative; }
.cSelect {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  @include r-regular; }
.options {
  display: none;
  position: absolute;
  top: 100%;
  width: 100.6%;
  left: -1px;
  z-index: 999;
  margin: 0 0;
  padding: 0 0;
  list-style: none;
  li {
    padding: 0 6px;
    margin: 0 0;
    padding: 0 10px;
    @include r-regular;
    &:not(:first-child) {
      border-top: none !important; }
    &:hover {
      background-color: #eee !important; } } }
.dropdown__trg,
.options li,.cSelect {
  padding: 0 13px 0 15px; }


.btn-group.mobile {
  .btn {
    i {
      margin-right: 7px;
      font-size: 16px; } } }
.dropdown__trg,
.cSelect,
.orderers .switch,
.SumoSelect >.CaptionCont>label,
#show-submenu,
.spoiler__trigger {
  &:after {
    content: '\e917';
    font-family: $if;
    font-size: 5px;
    color: #474747;
    transform: rotate(0);
    transition: $tr-fast;
    float: right; } }
.SumoSelect >.CaptionCont>label:after {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-40%);
  font-size: 4px; }

.dropdown {
  &.big-options.is-active {
    .dropdown__trg {
      &:after {
        transform: rotate(-180deg); } } } }
.cSelect {
  &.active {
    &:after {
      transform: rotate(-180deg); } } }

input {
  &[type="number"] {
    @include r-regular;
    padding: 0 !important;
    -moz-appearance: textfield;
    &::-webkit-inner-spin-button,
    &::-webkit-outer-spin-button {
      -webkit-appearance: none;
      -moz-appearance: button; }
    &:first-child {
      border-right: none; } }
  &[type="radio"],
  &[type="checkbox"] {
    position: absolute;
    opacity: 0;
    visibility: hidden;
    + label {
      @include nosel; } }
  &[type="checkbox"] {
    + label {
      background: #fff;
      margin-bottom: 0;
      @include r-regular;
      cursor: pointer; } }
  &.realCheckbox, &[type="radio"] {
    + label {
      font-size: 15px;
      background: transparent;
      border: none;
      display: flex;
      align-items: center;
      position: relative;
      &:after,&:before {
        width: 25px;
        height: 25px; }
      &:after {
        content: '\e903';
        font-family: $if;
        position: absolute;
        font-size: 11px;
        color: $orange;
        opacity: 0;
        visibility: hidden;
        text-align: center;
        line-height: 26px;
        left: 0; }
      &:before {
        content: '';
        display: inline-block;
        margin-right: 10px;
        background: #fff; } }
    &:checked {
      + label {
        &:after {
          opacity: 1;
          visibility: visible; } } } }
  &.realCheckbox + label {
    &:before {
      border: 1px solid $lbd; } }
  &[type="radio"] + label {
    &:before {
      border-radius: 50%;
      cursor: pointer;
      border: 1px solid #c6c6c6; } }
  &.text-checkbox {
    + label {
      display: inline-block;
      position: relative;
      padding: 6px 15px;
      line-height: 25px;
      border: 1px solid $lbd; }
    &:checked {
      + label {
        background: $orange;
        color: #fff;
        border-color: #fafafa; } } }
  &[type="search"] {
    text-transform: capitalize;
    @include r-medium;
    color: lighten($text,40%);
    &::placeholder {
      color: #818181; } } }
.filter li {
  &.active {
    label {
      &:after {
        visibility: visible;
        opacity: 1; } } } }

.SelectBox {
  padding-left: 15px 11px; }
.SumoSelect {
  cursor: pointer;
  &:hover,&:focus {
    >.CaptionCont {
      box-shadow: none;
      border-color: $lbd; } }
  >.optWrapper {
    border-color: $lbd;
    box-shadow: none;
    border-radius: 0; }
  &.open {
      >.CaptionCont {
          box-shadow: none;
          border-color: $lbd;
          >label {
            &:after {
              transform: translate(-50%,-50%) rotate(-180deg); } } } }
  >.optWrapper>.optionsli.opt:first-child,
  >.SumoSelect>.optWrapper>.options,
  >.CaptionCont {
    border-radius: 0;
    border-color: $lbd;
    &.is-active {
      background: $orange;
      color: #fff; }
    &:hover,&:focus {
      box-shadow: none;
      border-color: $lbd; }
    >span,>label {
      cursor: pointer; }
    >span {
      padding-right: 10px;
      line-height: 2; }
    >label {
      margin: 0;
      width: 27px;
      height: 97%;
      top: 1px;
      >i {
        opacity: 0.7;
        background-size: 97%;
        width: 11px;
        height: 11px; } } } }

.SumoSelect .select-all>span i,
.SumoSelect>.optWrapper.multiple>.options li.opt span i,
.SumoSelect .select-all>span i,
.SumoSelect>.optWrapper.multiple>.options li.opt span i, {

  border: 1px solid $lbd;
  background: transparent;
  background-image: none;
  border-radius: 0;
  width: 25px;
  height: 25px;
  box-shadow: none;
  &:after {
    content: '\e903';
    font-family: $if;
    position: absolute;
    font-size: 11px;
    color: $orange;
    opacity: 0;
    visibility: hidden;
    text-align: center;
    line-height: 26px;
    width: 100%; } }
.SumoSelect > .optWrapper.multiple > .options li.opt.selected span i,
.SumoSelect .select-all.selected > span i,
.SumoSelect .select-all.partial > span i {
  &:after {
    visibility: visible;
    opacity: 1; } }


.choosed-tags {
  span,.remove-trigger {
    display: inline-block;
    border-radius: 5px;
    height: 30px;
    line-height: 30px;
    font-size: 13px;
    padding: 0 10px;
    position: relative;
    cursor: default;
    background: #f4f4f4;
    &:after {
      font-family: $if;
      margin-left: 7px;
      cursor: pointer;
      font-size: 9px; } }
  span {
    pointer-events: none;
    margin-right: 2px;
    z-index: 1;
    &:after {
      content: "\e904";
      pointer-events: all;
      z-index: 2; } }
  .remove-trigger {
    @include r-regular;
    background: #b2b2b2;
    color: #fff;
    float: right;
    &:after {
      content: "\e905"; } } }


.order-panel {
  padding: 35px 0;
  &__title {
    color: $text2;
    @include r-regular;
    flex-shrink: 0; }
  &__btn-group {
    .btn {
      display: inline-block; } }
  .orderers {
    .switch {
      display: inline-block;
      padding: 1px 6px 2px;
      &.is-active {
        transform: rotate(-180deg); } }
    .btn {
      padding: 8px 10px;
      margin-left: 27px;
      border: 2px solid transparent;
      color: $text;
      &[data-order="onlyNew"] {
        &:after {
          display: none; } }
      &.is-active,&:hover {
        border-color: $orange; }
      &.is-active {
        &.down {
          &:after {
            transform: rotate(0); } } } } } }
.gostinka {
  width: 70px;
  height: 35px;
  color: transparent;
  overflow: hidden;
  margin-top: 1px;
  &::-webkit-scrollbar {
    width: 0; }
  &:after {
    content: 'Гост.';
    position: absolute;
    left: 9px;
    top: 34px;
    line-height: 40px;
    font-size: 14px;
    color: $text;
    @include r-regular;
    text-align: center; }
  &:before {
    content: '\e917';
    font-family: $if;
    left: 52px;
    top: 35px;
    color: #474747;
    font-size: 4px;
    position: absolute; } }
