.apartaments-table {
  padding: 50px 0 60px;
  h3 {
    margin-bottom: 15px; } }
.box {
  &__mobileContainer {
    margin-top: 22px; }
  &__mobileItem {
    width: 98%;
    margin: 15px 2px 2px;
    .cardAside {
      display: block;
      color: #212;
      box-shadow: 0 2px 9px 1px rgba(0, 1, 1, 0.31);
      &:active,&:hover {
        color: #212; } }
    .cardAside {
      &__r-count {
        display: block;
        font-size: 23px !important; } } }
  &__mobileContainer {
      display: none; }
  .slick-slide {
    &:not(:first-child) {
      table tbody {
        border-left: 5px solid #fff; } } }
  .slick-list {
      background: #d7d7d7; }
  &__signs {
    margin-left: 50px;
    position: relative;
    display: block; }
  &__sign {
    display: inline-block;
    text-align: center;
    &:not(:first-child) {
      margin-left: 75px; }
    >span {
      display: block;
      margin-top: 5px;
      font-size: 11px; }
    >div {
      width: 22px;
      height: 22px;
      background: green;
      margin: 0 auto; }
    &.r-1 {
      >div {
        background: #47befb; } }
    &.r-2 {
      >div {
        background: #22c3b2; } }
    &.r-3 {
      >div {
        background: #835feb; } }
    &.r-nan {
      >div {
        background: #ececec; } } }

  .tabs-block__cContainer {
    margin-top: 0; }
  &__tabs {
    padding-right: 25px;
    margin-right: 12px;
    max-height: 500px;
    @include sbar;
    .btn {
      padding: 8px 8px 4px;
      line-height: 1;
      display: block;
      width: 165px;
      height: 53px;
      text-align: left;
      border-color: transparent;
      margin: 0 10px 10px 0 !important;
      &:last-child {
        margin-bottom: 0 !important; }
      p {
        color: #303030;
        margin-bottom: 0;
        span {
          font-size: 15px; } }
      >span {
        font-size: 11px;
        &.lightB {
          color: #22c3b2; } } } }
  &__outerLarge {
    display: flex; }
  &__outerMiddle {
    height: 96%; }
  &__outerSmall {
    width: 100%;
    position: relative; }
  &__counter {
    position: absolute;
    width: 10px !important;
    margin-top: 59px;
    td div,caption {
      background: transparent !important; }
    caption {
      color: $text !important; } }

  &__filters {
    margin-bottom: 10px;
    z-index: 5;
    span {
      margin-right: 10px; }
    .bf-btn {
      padding: 6px 10px;
      border: none;
      font-size: 15px;
      line-height: {}
      @include r-regular;
      display: inline-block;
      cursor: pointer;
      border: 2px solid transparent;
      &.active {
       border-color: $orange; } } }
  &__topNav {
    text-align: center;
    position: absolute;
    top: -47px;
    left: 20%;
    margin-bottom: 0;
    z-index: 10; }
  &__inner {
    display: none !important;
    padding-top: 60px;
    margin-left: 50px;
    position: absolute;
    z-index: 10;
    &.active {
      overflow: visible !important;
      &:after,&:before {
        display: block; } }
    &:before,&:after {
      display: none; }
    &:before {
        content: "";
        position: absolute;
        z-index: -1;
        background: #525555;
        width: 100%;
        -webkit-clip-path: polygon(2% 0,80% -150px,100% 100%,0% 77%);
        clip-path: polygon(2% 0,80% -150px,100% 100%,0% 77%);
        padding: 33px;
        top: 10px; }
    &:after {
      content: '';
      position: absolute;
      background-image: url(../img/roof.svg);
      width: 147px;
      height: 70px;
      background-size: 100%;
      background-repeat: no-repeat;
      top: -10px;
      right: 0; }
    &.slick-initialized {
      display: flex !important; }
    &.b-priceForMfilter {
      td p {
        &:not(.b-priceForM) {
          display: none; } } }
    &.b-rCountFilter {
      td p {
        &:not(.b-rCount) {
          display: none; } } }
    &.b-priceFilter {
      td p {
        &:not(.b-price) {
          display: none; } } } }
  position: relative;
  .slick-list {
    // margin: 0 -10px
    // overflow-x: scroll
 }    // background: #d7d7d7
  .slick-track {
    width: 100% !important;
    // transform: none !important
    display: flex; }
  .slick-slide {
    // margin: 0 10px
    width: 100% !important;
    min-width: 50%; }

  table {
    @include nosel;
    table-layout: auto;
    // border-collapse: separate
    // border-spacing: 3px
    border: none;
    thead tr {
      border-bottom: none; }
    tr {
      border: none; }
    thead,tbody,caption {
      display: table;
      width: 100%;
 }      // table-layout: fixed
    tr,td {
      line-height: normal;
      vertical-align: middle; }
    td {
      position: relative;
      z-index: -1;
      padding: 3px 2px;
      background: #fff;
      >div {
        background: #ececec;
        width: auto;
        min-width: 5px;
        height: 22px;
        line-height: 22px; }
      p {
        color: #fff;
        padding: 0 10px;
        font-size: 12px;
        @include r-regular;
        // +coverdiv
        &.r-1 {
          background: #47befb; }
        &.r-2 {
          background: #22c3b2; }
        &.r-3 {
          background: #835feb; } } } }
  &__inner {
  	counter-reset: entrance;
  	width: 1170px; }
  table caption {
    font-size: 15px;
    color: #fff;
    padding: 0;
    margin: 0;
    background: #9b9d9e;
    text-align: center;
    line-height: 33px;
    @include r-regular;
    width: calc(100% + 1px); }
  &__topNav {
    &:before {
      content: 'Подъезд: ';
      margin-right: 10px; }
    li {
      display: inline-block;
      counter-increment: slide +1;
      &:after {
          content: counter(slide);
          display: none; }
      &:not(:first-child) {
        margin-left: 5px; }
      button {
        padding: 7px 10px;
        border: 2px solid transparent;
        line-height: 1;
        background: #fff; }
      &.slick-active {
        button {
          border-color: $orange; } }
      // &:last-child
      //   &:after
      //     display: inline-block
      //     pointer-events: all
      //     cursor: pointer
 } }      //     margin-left: 10px
  .order-panel {
  	&__title {
  		font-size: 15px; }
  	.orderers .btn {
  		margin-left: 0;
  		font-size: 15px;
  		padding: 7px 10px;
  		.switch {
  			display: none; } } }
  &__dots {
  	z-index: 99;
  	position: relative;
  	background: blue;
  	display: flex;
  	li {
  		display: inline;
  		height: 20px;
  		flex: 1 1 auto;
  		text-align: center;
  		button {
  			background: $green;
  			&:before {
  				padding: 10px;
	  			content: 'Подъезд';
	  			margin-right: 10px;
	  			display: inline-block;
	  			color: #fff;
	  			height: 100%;
	  			pointer-evetns: all;
	  			background: $green; } }
  		&:not(:first-child) {
  			margin-left: 30px; } } }
  &__inner {
    width: 100%;
    display: flex;
    flex-direction: column-reverse; } }

@media screen and (max-width: 1900px) {
  .box__topNav {
    left: 27%; } }

@media screen and (max-width: 1200px) and (min-width: 1100px) {
  .apartaments-table {
    min-width: 85vw;
    margin-left: -15vw;
    min-height: 70vh; }
  .box {
    &__tabs {
      display: none; }
    &__topNav {
      left: 46% !important; }
    &__outerMiddle {
      height: 57vh; } } }

@media screen and (max-width: 1100px) {
  .box {
    .load-nav {
      margin-top: 20px; }
    &__mobileFilters {
      margin-bottom: 15px;
      >span {
        @include r-regular;
        font-size: 18px;
        line-height: 3; }
      >div {
        margin: 10px 0; } }
    &__outerLarge,&__filters {
      display: none; }
    &__mobileContainer {
      display: block; } } }

@media screen and (max-width: 570px) {
  .box__mobileItem {
    .cardAside {
      &__price {
        div:not(.small) {
          font-size: 23px; }
        div.small {
          font-size: 15px;
          margin-left: 10px; } } } }

  .box {
    .load-nav {
      margin-top: 25px; } } }
