@import "helpers/mixins";
@import "helpers/iconfont";

@include font(RobotoBold, RobotoBold);
@include font(RobotoMedium, RobotoMedium);
@include font(RobotoRegular, RobotoRegular);
@include font(RobotoLight, RobotoLight);


@include font(OpenSansBold, OpenSansBold);
@include font(OpenSansSemiBold, OpenSansSemiBold);
@include font(OpenSansRegular, OpenSansRegular);

@include font(rouble, rouble);

@mixin r-bold {
  font-family: RobotoBold;
  font-weight: normal; }
@mixin r-medium {
  font-family: RobotoMedium;
  font-weight: normal; }
@mixin r-regular {
  font-family: RobotoRegular;
  font-weight: normal; }
@mixin r-light {
  font-family: RobotoLight;
  font-weight: normal; }

@mixin os-bold {
  font-family: OpenSansBold;
  font-weight: normal; }
@mixin os-sbld {
  font-family: OpenSansSemiBold;
  font-weight: normal; }
@mixin os-reg {
  font-family: OpenSansRegular;
  font-weight: normal; }


.rub {
  font-family: rouble !important; }
