.full-slider {
	img {
		max-width: 100%;
		height: auto; }
	.slick-slide {
		position: relative;
		a {
			@include coverdiv; }
		img {
			@include coverdiv; } }
	.slick-list {
		overflow: hidden; }
	&__caption {
		position: absolute;
		background: lighten(#6a6c74,8%);
		color: #fff;
		@include r-regular;
		right: 0;
		bottom: 0;
		padding: 8px 20px; }
	&__thumbs {
		width: 100%;
		.slick-list {
    		margin: 0 -22px; }
		&.slick-slider {
			// .slick-list
			// 	overflow: visible
			.slick-track {
				margin: 0; }
			// 	display: flex
			// 	width: 100% !important
			// 	transform: none !important
			// 	flex-wrap: wrap
			// 	justify-content: space-between
			// 	&:after
			// 		content: ''
			// 		flex: auto
			.slick-list {
    			margin: 0 -5px; }
			.slick-slide {
				margin: 10px 5px;
				// flex: 0 1 calc(17% - 11px)
				width: 100%;
				height: 70px;
				box-sizing: border-box;
				cursor: pointer;
				> div img {
					@include coverdiv; }
				// &:not(:nth-child(6n))
				/// margin-right: 10px
				img {
					object-fit: cover;
 } } }					// border: 3px solid transparent
		// .slick-track
		// 		&:not(:first-child)
		// 			margin-left: 18px
		.slick-current {
			position: relative;
			// border-color: $orange !important
			// display: flex
			// justify-content: space-between
			&:after {
				content: '';
				@include coverdiv;
				border: 3px solid $orange; } }
		// .slick-current
		/// .slick-slide
 }		//// border-color: $orange
	.s-a {
		z-index: 2;
		&__prev {
			left: 22px; }
		&__next {
			right: 22px; } } }
