.header {
	z-index: 9999;
	position: relative;
	&__top {
		background: #fff;
		.nav li {
			&:hover,&.active {
				a {
					color: $green; } } }
		.logo {
			margin: 16px 0;
			width: 170px; } }
	&__bottom {
		background: $green;
		position: relative;
		box-shadow: 0 2px 5px rgba(0, 0, 0, 0.25);
		.collapse {
			display: block; } }
	&__rSide {
		width: auto; }
	li {
		cursor: pointer; } }

.header-top-nav {
	padding-left: 10px;
	.close-trigger {
		display: none; }
	.nav {
		white-space: nowrap;
		li {
			a {
				text-decoration: none;
				border: none; } } } }

#show-menu,
#show-submenu {
	display: none; }
#show-menu {
	margin-left: 25px;
	margin-bottom: 0;
	width: 50px;
	background: #202020;
	height: 9px;
	position: relative;
	transition: $tr;
	&:after,&:before {
		content: '';
		position: absolute;
		left: 0;
		width: 50px;
		height: 9px;
		background: #202020;
		transition: $tr; }
	&:after {
		bottom: -17.5px; }
	&:before {
		top: -17.5px; }
	&.is-active {
		transform: rotate(-45deg);
		&:after {
			display: none; }
		&:before {
			transform: rotate(90deg);
			top: 0; } } }

#show-submenu {
	&:after {
		color: #fff;
		margin: 7px 0 0 12px; } }


.header-bottom-nav {
	a {
		line-height: 46px;
		position: relative;
		padding: 0;
		flex: 1 1 auto;
		text-align: center;
		z-index: 1;
		&:nth-child(1) {
			&.active {
				&:after {
					transform: skew(0deg,7deg); }
				&:before {
					transform: skew(0deg,-7deg);
					right: 1px; } } }
		&:nth-child(3),
		&:nth-child(4),
		&:nth-child(5) {
			&.active {
				&:before,&:after {
					top: 20.5px; } } }
		&:nth-child(4) {
			&.active {
				&:after {
					transform: skew(0deg,4deg); }
				&:before {
					transform: skew(0deg,-4deg);
					right: 1px; } } }
		&.active,&:hover {
			background: $orange; }
		&.active {
			&:before,&:after {
				content: "";
				position: absolute;
				z-index: -1;
				background: $orange;
				width: 50.2%;
				height: 12px;
				top: 20px;
				padding-bottom: 31px; }
			&:before {
				right: 0;
				transform: skew(0deg, -6deg); }
			&:after {
				left: 0;
				transform: skew(0deg, 6deg); } } } }
.phone {
	text-align: right;
	white-space: nowrap;
	&:active, &:hover, &:visited, span {
		color: $text; }
	&__top {
		font-size: 20px;
		line-height: 1;
		letter-spacing: .67px;
		@include r-medium; }
	&__bottom {
		font-size: 14px;
		display: block; } }


.footer {
	color: #fff;
	&__main {
		background: #6a6c74;
		padding: 30px 0;
		#{$a-tags} {
			&:not(.footer__social-link) {
				font-size: 15px;
				line-height: 26px; } } }
	&__additional {
		background: #2f3034;
		padding: 13px 0;
		span, a {
			font-size: 14px; } }
	&__copyright-before {
		position: absolute;
		bottom: 0; }
	#{$a-tags} {
		@include r-light;
		letter-spacing: .3px; }
	&__outer {
		display: table;
		height: 100%; }
	&__inner {
		display: table-cell;
		height: 100%;
		vertical-align: bottom; }
	&__add-column {
		.links a {
			display: block;
			line-height: 1.5 !important;
			margin-bottom: 5px; } }
	&__social-link {
		font-size: 25px;
		display: inline-block;
		margin-right: 12px;
		border: 2px solid #fff;
		position: relative;
		margin-top: 38px;
		@include btn45;
		&:hover {
			color: #fff;
			background: $orange;
			border-color: $orange; } } }

.footer-contacts a,.footer__add-column .links a,
.f-list-group__mnu-link,.dev-info a {
	border-bottom: 1px solid #fff;
	&:hover {
		border-color: transparent; } }
.f-list-group,
.footer-contacts {
	ul > li {
		display: block; } }
.f-list-group {
	ul li {
		font-size: 13px;
		line-height: 26px;
		cursor: pointer;
		a {
			border-bottom: 1px solid transparent; }
		&:hover {
			a {
				border-color: #fff; } } }
	&__head {
		@include r-regular;
		font-size: 18px;
		margin-bottom: 10px; } }
.footer-contacts {
	min-width: 375px;
	ul li {
		font-size: 15px;
		line-height: 28px; }
	&__head {
		margin-bottom: 10px;
		@include r-regular;
		font-size: 18px; } }
